.faq {
  padding: 30px;
  background: #FFFFFF;
  border-radius: 8px;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.1);
  margin: 0 0 20px 0;
}
.faq__title {
  text-align: start;
  color: #151A18;
  margin: 0 0 25px 0;
}

.accordion__item:not(:last-child) {
  margin: 0 0 10px 0;
}

.accordion__header {
  display: flex;
  position: relative;
  align-items: center;
  font-family: var(--font-title);
  padding: 22px 55px 21px 20px;
  background: #F3F5F6;
  border-radius: 8px;
  font-size: 18px;
  line-height: 27px;
  color: #151A18;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.accordion__header::after {
  display: inline-block;
  position: absolute;
  content: "";
  right: 30px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0px 7px 9px;
  border-color: transparent transparent #858585 transparent;
  transform: rotate(180deg);
  margin: 0 0 0 6px;
  transition: transform 0.3s ease, border-color 0.3s ease;
}
.accordion__header::before {
  content: "";
  width: 28px;
  height: 28px;
  min-width: 28px;
  min-height: 28px;
  margin: 0 10px 0 0;
  background: url("../img/general-block-image/faq-image.svg") no-repeat center/contain;
  display: inline-block;
}

.accordion__item_slidedown .accordion__header,
.accordion__item_show .accordion__header {
  border: none;
}

.accordion__header:hover {
  background-color: #c8c8d0;
}

.accordion__content {
  display: flex;
  flex-direction: column;
  padding: 25px 35px 15px;
  background: transparent;
}
.accordion__content p {
  font-size: 14px;
  line-height: 26px;
  color: #858585;
}
.accordion__content p:not(:last-child) {
  margin: 0 0 20px 0;
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.accordion__item_show .accordion__header::after, .accordion__item_slidedown .accordion__header::after {
  transform: rotate(0deg);
  border-color: transparent transparent #0B764F transparent;
}

@media screen and (max-width: 1024px) {
