  margin: 0 25px 0 0;
}
.footer__text {
  color: #858585;
  font-size: 13px;
  line-height: 20px;
}
.footer__text-link {
  white-space: nowrap;
  color: #FFFFFF;
  text-decoration: underline;
}
.footer__cookie {
  position: fixed;
  width: 80%;
  padding: 20px 40px;
  border: 1px solid #FFFFFF;
  background: linear-gradient(180deg, #085A3D 0%, #038941 100%);
  z-index: 10;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 30px;
  display: flex;
  align-items: center;
}
.footer__cookie .close-cookie {
  cursor: pointer;
  position: absolute;
  right: 18px;
  top: -8px;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid #FFFFFF;
  background: linear-gradient(180deg, #085A3D 0%, #038941 100%);
}
.footer__cookie .close-cookie span {
  position: relative;
  width: 26px;
  height: 26px;
  display: block;
}
.footer__cookie .close-cookie span::before, .footer__cookie .close-cookie span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60%;
  height: 2px;
  background-color: #FFFFFF;
}
.footer__cookie .close-cookie span::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.footer__cookie .close-cookie span::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.footer__cookie._hidden {
  display: none;
  visibility: hidden;
}
.footer__cookie-img {
  min-width: 40px;
  max-width: 41px;
  margin: 0 15px 0 0;
}
.footer__cookie-info {
  display: flex;
  align-items: center;
  flex: 1 1 auto;
}
.footer__cookie-text {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.01em;
  color: #FFFFFF;
  flex: 1 1 auto;
  margin: 0 21px 0 0;
}
.footer__cookie-btn {
  display: inline-block;
  padding: 12px 25px 10px;
  white-space: nowrap;
}

@media screen and (max-width: 1600px) {
  .footer__cookie {
    width: 70%;
  }
}
@media screen and (max-width: 1170px) {
  .footer__row {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 30px;
    margin: 0 30px 0 0;
  }
  .footer__info {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__sponsor {
    margin: 0 0 25px 0;
  }
  .footer__item:not(:last-child) {
    margin: 0;
  }
}
@media screen and (max-width: 1024px) {
  .footer {
    overflow-x: hidden;
  }
  .footer__link {
    font-size: 18px;
    line-height: 18px;
  }
}
@media screen and (max-width: 750px) {
  .footer__row {
    display: grid;
    grid-template-columns: auto auto;
    gap: 30px;
  }
  .footer__cookie {
    width: 60%;
  }
}
@media screen and (max-width: 600px) {
  .footer {
    padding: 30px 0;
  }
  .footer__subtext {
    margin: 0 0 40px 0;
  }
  .footer__subtext p {
    font-size: 13px;
    line-height: 15px;
  }
  .footer__wrapper {
    flex-direction: column;
    align-items: stretch;
    margin: 0 0 40px 0;
  }
  .footer__row {
    margin: 0 0 40px 0;
    grid-template-columns: calc(50% - 15px) calc(50% - 15px);
    gap: 30px 15px;
  }
  .footer__item {
    max-width: 100%;
  }
  .footer__social {
    align-self: center;
  }
  .footer__sponsor {
    justify-content: center;
    margin: 0 0 15px 0;
  }
  .footer__sponsor-item:not(:last-child) {
    margin: 0 15px 0 0;
  }
  .footer__text {
    font-size: 11px;
    line-height: 16px;
  }
  .footer__cookie {
    align-items: flex-start;
    padding: 20px 15px;
    bottom: 10px;
    width: 98%;
  }
  .footer__cookie-text {
    font-size: 13px;
    line-height: 22px;
    margin: 0 15px 0 0;
  }
  .footer__cookie-info {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__btn-top {
    z-index: 10;
    bottom: 29px;
    right: 20px;
    width: 45px;
    height: 43px;
  }
  .footer__btn-top span {
    border-width: 0 7px 9px;
  }
}